.container-landing {
  display: flex;
  max-width: 1200px;
  margin: auto;
   height: 100vh; 
  position: relative;
  z-index: 80;
}
.container-text-landing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  max-width: 25rem;
}
.img-container {
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: flex-end;
}
.me {
  margin: auto;
  height: 25rem;
  border-radius: 50%;
  border: 5px solid var(--title);
  width: auto;
  animation: me infinite 4s;
}
.text-container-landing{
  justify-content: center;
  text-align: center;
  margin: auto;
}

@keyframes me {
  0% {
    -webkit-transform: rotateZ(0deg);
  }
  25% {
    -webkit-transform: rotateZ(3deg);
  }
  50% {
    -webkit-transform: rotateZ(3deg);
  }
  100% {
    -webkit-transform:rotateZ(0deg);
  }
}
.title-landing {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 800px) {
  .container-text-landing {
    margin: auto;
  }
}
@media screen and (max-width: 450px) {
  .me {
    display: none;
  }
  .container-text-landing {
    margin: auto;
  }
}
