nav {
  background: var(--title);
  box-shadow: 0 0 15px var(--bgc2);
  position: fixed;
  width: 100vw;
  z-index: 99;
  top: 0;
}
ul {
  list-style-type: none;
}
.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
.nav-toggle {
  font-size: 1.5rem;
  color: var(--bgc);
  background: transparent;
  border-color: transparent;
  transition: var(--transition);
  cursor: pointer;
}
.nav-toggle:hover {
  color: var(--bgc);
  transform: rotate(90deg);
}
.links a {
  font-family: 'Oswald', sans-serif;
  color: var(--bgc);
  font-size: 2rem;
  font-weight: 900;
  letter-spacing: 1;
  display: block;
  justify-content: center;
  text-align: center;
  padding: 0.5rem 1rem;
  transition: var(--transition);
}
.links-container {
  height: 0;
  overflow: hidden;
  transition-delay: var(--transition);
}
.show-container {
  height: 100vh;
  transition: var(--transition);
  justify-content: center;
  text-align: center;
}
.social {
  display: none;
}
.Author {
  display: none;
}

@media screen and (min-width: 830px) {
  .nav-center {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    padding: 0.4rem 1rem;
  }
  .nav-header {
    padding: 0;
  }
  .nav-toggle {
    display: none;
  }
  .links-container {
    height: auto !important;
  }
  .links {
    display: flex;
    align-items: center;
    justify-content: center;
    margin:  auto;
  }
  .links a {
    font-family: 'Oswald', sans-serif;
    padding: 0 1rem;
    color: var(--bgc);
    font-size: 1.2rem;
    margin: 0 0.5rem;
  }

  .social,
  .social-about {
    display: flex;
    font-weight: 600;
    font-size: 2rem;
  }
  .social a {
    text-align: center;
    margin: 0 1.3rem;
    color: var(--bgc);
    transition: var(--transition);
  }
  .Author {
    display: flex;
    font-size: 2rem;
    font-weight: 900;
  }
  .author a {
    font-family: 'Oswald', sans-serif;
    color: var(--bgc);
    text-transform: uppercase;
  }

}
