* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap");
body {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  /* max-width:100vw; */
  overflow-x: hidden;
  /* background-color:black; */
}

:root {
  /* navbar color */
  --title: #a852a5;
  --text-dark: #962e93;
  --buble-dark: #ffb8fbd3;
  --buble-semi-dark: #ff85f97c;
  --buble-darkest: #ea7ee5d1;
  --buble-gradient: #ff55f68f;
/* */
  --color-main: #151515;
  --color-main2: rgb(0, 185, 185);
  --color-main3: rgb(53, 168, 168);
  --color-green: #36f936;
  --color-red: rgb(233, 233, 233);
  --color-yellow: yellow;
  --color-yellow2: rgb(255, 255, 160);
  --med: 10px 10px;
/*  */
  --buble-light: #e7d0e6e4;
  --buble-lighter: #f68ff155;
  --text-light: rgb(218, 218, 218);
  --bgc: White;
  --bgc2: black;
  --transition: ease-in-out 1s;
  --max-width: 1200px;
}

a {
  text-decoration: none;
}
h1,
h2,
p,
span {
  color: var(--text-dark);
}
h1 {
  font-size: 2.8rem;
  text-transform: uppercase;

  font-family: "Oswald", san-serif;
}
h2 {
  font-size: 1.8rem;
  font-family: "Helvetica Neue", sans-serif;
}
p {
  font-size: 1.5rem;
  font-family: "Helvetica Neue", sans-serif;
}
span {
  font-weight: 800;
  text-transform: uppercase;
}

.resume {
  padding: 0.5rem 3rem;
  max-width: 18rem;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 1rem auto;
  background-color: var(--text-dark);
  font-size: 1.3rem;
  font-family: "Oswald", serif;
  color: var(--bgc);
  border-radius: 10px;
}
.resume:hover {
  background-color: var(--title);
  transition: ease-in-out 1s;
}

@media screen and (max-width: 850px) {
  body {
    overflow-x: hidden;
  }
  h1 {
    font-size: 1.5rem;
  }
  h2 {
    font-size: 1.2rem;
  }
  p {
    font-size: 1rem;
  }
  .container-about-description {
    flex-wrap: wrap-reverse;
    padding: 1rem;
  }
  .me {
    display: none;
  }
  .container-tecnologies {
    justify-content: center;
    margin: auto;
  }
  .social-about a {
    color: var(--text-dark);
    font-size: 2rem;
  }
  .about-title {
    text-align: center;
    margin: 1.2wrem auto;
  }
  .buble{
    display: none;
  }
}
