.container-bubles{
  max-width: 800px;
  overflow-y: hidden;
}

.buble {
  position: absolute;
  background-color: var(--buble-dark);
  border-radius: 50%;
  width: 8rem;
  height: 8rem;
   animation: buble 5s linear;
  transition: 1s ease-in-out;
}

.buble.a {
  left: -10%;
  top: -30%;
  overflow: hidden;
  height: 45rem;
  width: 45rem;
  z-index: -10;

background-color: var(--buble-dark);

  border: 0px;
}

.buble.b{
  bottom:0;
  right:45%;
  width: 350px;
  height:350px;
  background: var(--buble-dark);
  z-index: 0;
  border: 0px;
}
.buble.f {
  bottom: 12%;
  height: 30em;
  width: 30em;
  right: 12%;
  background-color: rgba(255, 43, 255, 0.501);
   animation: buble 5s linear infinite;
  z-index: 7;
}
.buble.g {
  bottom: 18%;
  left: 10%;
  animation: buble 5s linear;
  background-color: var(--bgc);
  z-index: 6;
}
.buble.i{
  bottom: 5%;

  z-index: -1;
}
@keyframes buble {
  0% {
    -webkit-transform: scale(1);
  }
  25% {
    -webkit-transform: scale(0.8);
  }
  50% {
    -webkit-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
  }
}
