.gallery {
  background-color: var(--buble-dark);
}
.project-static{
  margin: 1rem;
  width: 20rem;
  background-color: var(--title);
  padding: 0.5rem;
  box-shadow: 5px 5px 10px var(--bgc2);
  border-radius: 10px;
}
.projects-text{
  color:var(--bgc);
  font-size: 1rem;
  font-family: 'Helvetica-Neue',sans-serif;
  text-align: center;
  justify-content: center;
  padding: 0.3rem;
}
.projects-title {
  display: flex;
  text-align: center;
  justify-content: center;
}
.galery-static-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.img-project {
  object-fit: cover;
  border-radius: 10px;
  height: auto;
  width: 100%;
  margin: auto;
  min-height:12rem;
}


.button-container-static {
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.button-link {
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  border: 1px solid var(--bgc2);
  border-radius: 10px;
  padding: auto;
  height: 2rem;
  width: 100%;
  margin: auto;
  color: var(--bgc);
  font-weight: 800;
  background-color: var(--text-dark);
}
.button-link:hover {
  background-color: var(--buble-light);
  color: var(--bgc2);
  transition: ease-in-out 1.3s;
}
.button-dinamic {
  width: 10rem;
  height: 3rem;
  border: 1px solid var(--bgc2);
  border-radius: 10px;
  background-color: var(--text-dark);
  color: var(--bgc);
  font-weight: 800;
  margin: 1rem auto;
}
.button-dinamic:hover {
  background-color: var(--buble-darkest);
  color: var(--bgc2);
  transition: ease-in-out 1s;
}
h3 {
  text-transform: uppercase;
  margin: 0 0 0.5rem 0;
  font-family: "Oswald", serif;
  color: var(--bgc);
}
