#Anime{
  background-color:var(--color-main)
}
.Anime-bg {
  margin: auto;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.container-anime {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}
.anime {
  margin: 1rem;
  width: 23rem;
  height: 40rem;
  background-color: var(--bgc2);
  border-radius: 10px;
  border: 1px solid black;
  padding: 1rem;
}
.start-anime,
.end-anime,
.anime-subtitle,
.anime-text,
.type-anime {
  color: var(--bgc);
}

.green,
.red,
.yellow {
  font-weight: 900;
}
.green {
  color: var(--color-green);
}
.red {
  color: var(--color-red);
}
.yellow {
  color: var(--color-yellow);
}
.anime-title {
  font-size: 1.3rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  font-family: "Oswald", sans-serif;
  color: var(--color-yellow);
}
.anime-text {
  font-size: 1rem;
}
.img {
  object-fit: cover;
  height: 20em;
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.a,
.b .img {
  cursor: pointer;
}

.input-container {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  border-radius: 10px;
  height: 2rem;
}
input{
  width: 100%;
  height: 2rem;
  border-radius: 10px;
}
.a,
.b {
  height: 2rem;
  width: 100%;
  font-weight: 1000;
  text-align: center;
  border: solid 1px;
}
.input-container {
  padding: 1rem;
  background-color: var(--bgc2);
}
.a {
  background-color: var(--color-yellow);
}
.b {
  background-color: var(--color-yellow);
}
@media screen and (max-width: 800px) {
  .anime {
    flex-wrap: wrap;
    width: 70vw;
    align-items: center;
    overflow: hidden;
    justify-content: center;
    height: auto;
  }
}
