.container-about {
  display: flex;
  max-width: 1200px;
  margin: 4rem auto;
}
.container-about-img {
  display: flex;
  margin: auto;
  flex-direction: column;
}
.container-about-description {
  display: flex;
  margin: auto;

}
.container-about-text {
  display: flex;
  margin: auto;
  flex-direction: column;
  flex-wrap: wrap;
  padding:1rem;
}

.text-about-me {
  font-size: 1.2rem;
}

.container-tecnologies {
  display: flex;
  flex-direction: column;

}
.flex-tecnologies {
  text-align: center;
  padding: 0.4em;
}
.tec-list {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 600;
  font-family: "Oswald", serif;
  color: var(--title);
}

.social-about a{
    color: var(--text-dark);
}
.social-about-container{
    margin: auto;
}
.social-link{
    line-height: 1.5;
    text-align: center;
    justify-content: center;
}
.subtitle-about{
    font-family: 'Oswald', serif;
    text-transform: uppercase;
}
.me.a{
    transform: scaleX(-1);
}